import React from "react";
import loginImg from "../../login.png";
import axios from 'axios';
import "../../LoginApp.css";

const emailRegex = RegExp(
  /([a-zA-Z0-9._-]+@([a-zA-Z0-9_-]+\.)+[a-zA-Z0-9_-]+)/
);
var errorsFound="";
const formValid = ({ formErrors, ...rest }) => {
  let valid = true;

  // validate form errors being empty
  Object.values(formErrors).forEach(val => {
    val.length > 0 && (valid = false);
  });

  // validate the form was filled out
  Object.values(rest).forEach(val => {
    val === null && (valid = false);
  });

  return valid;
};

export class Register extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      userName: null,
      email: null,
      password: null,
      formErrors: {
        userName: "",
        email: "",
        password: "",
        isFormComplete: false
      }
    };
    //this.onChange = this.onChange.bind(this);
    //this.handleChange = this.handleChange.bind(this);

  }

  handleSubmit = e => {
    e.preventDefault();
      let formErrorsToChange = { ...this.state.formErrors };
      formErrorsToChange.isFormComplete=false; //stop users from submitting more than one form
      //let formState= {...this.state};
      this.setState({
            formErrors: formErrorsToChange
         });
      //this.setState({isFormComplete: false})
      console.log(`
        --SUBMITTING--
        User Name: ${this.state.userName}
        Email: ${this.state.email}
        Password: ${this.state.password}
      `);
      //promises
      //check values before sending props propergrate when the state changes. axiumns how to manage data  
      //react router //difference between component and container
      var targetUrl = 'http://localhost:4000/register'
      axios.post(targetUrl, this.state)
      .then(res => {
        console.log(res);
        console.log(res.data);
      })
      /*
      fetch( targetUrl , { //look up redux, hoc pattern 
        method: "POST",
        headers: {
          'Content-type': 'application/json'
        },
        body: JSON.stringify(this.state)
    })//.then((result) => result.json())
    //.then((info) => { console.log(info); })*/
  };

  handleChange = e => {
    e.preventDefault();
    const { name, value } = e.target;
    let formErrors = { ...this.state.formErrors };
    let formState= {...this.state}

    switch (name) {
      case "userName":
        formErrors.userName =
          value.length < 3 ? "minimum 3 characaters required" : "";
        break;
      case "email":
        formErrors.email = emailRegex.test(value)
          ? ""
          : "invalid email address";
        break;
      case "password":
        formErrors.password =
          value.length < 6 ? "minimum 6 characaters required" : "";
        break;
      default:
        break;
    }
    formState.formErrors=formErrors
    if(formValid(formState))
    {
      formErrors.isFormComplete=true;
    }else{
      formErrors.isFormComplete=false;
    }

    this.setState({ formErrors, [name]: value }, () => console.log(this.state));
  };

  render() {
    const { formErrors } = this.state;
    return (
      <div className="base-container" ref={this.props.containerRef}>
        <div className="header">Register</div>
        <div className="content">
          <div className="image">
            <img src={loginImg} />
          </div>
          <div className="form">
            <div className="form-group">
            <label htmlFor="userName">User Name:</label>
              <input
                className={formErrors.userName.length > 0 ? "error" : null}
                placeholder="User Name"
                type="text"
                name="userName"
                noValidate
                onChange={this.handleChange}
              />
              {formErrors.userName.length > 0 && (
                <span className="errorMessage">{formErrors.userName}</span>
              )}
            </div>
            <div className="form-group">
            <label htmlFor="email">Email</label>
              <input
                className={formErrors.email.length > 0 ? "error" : null}
                placeholder="Email"
                type="email"
                name="email"
                noValidate
                onChange={this.handleChange}
              />
              {formErrors.email.length > 0 && (
                <span className="errorMessage">{formErrors.email}</span>
              )}
            </div>
            <div className="form-group">
            <label htmlFor="password">Password</label>
              <input
                className={formErrors.password.length > 0 ? "error" : null}
                placeholder="Password"
                type="password"
                name="password"
                noValidate
                onChange={this.handleChange}
              />
              {formErrors.password.length > 0 && (
                <span className="errorMessage">{formErrors.password}</span>
              )}
            </div>
          </div>
        </div>
        <div className="footer">
        <button 
        type="submit"
        onClick={this.handleSubmit} 
        disabled={!(formErrors.isFormComplete)}  
        className="btnlogin">
            Register
          </button>
        </div>
      </div>
    );
  }
}
