import React, {useState,useEffect} from 'react'
import * as ReactBoostrap from "react-bootstrap";
import axios from 'axios';
import roomImg from '../../onevsone.jpg';
import "./Box.css";
const targetUrl = 'http://localhost:7000/games/rooms';
const joinRoomUrl = 'http://localhost:7000/games/joinroom'

const config = {
    headers: { Authorization: `Bearer ${localStorage.getItem('accessToken')}` }
};
const getRooms = () => axios.get(targetUrl,config)
        .then(res => { return res.data})
        .catch(error =>{alert(error)})

function GenerateRooms() {
  const [items, setItems] = useState();

  useEffect(() => {
    getRooms().then(data => setItems(data));
  }, []);

  function isRoomFull(card)
  {
    let user = localStorage.getItem('user')
    console.log(user)
    let accesstoken= localStorage.getItem('accessToken')
    console.log(accesstoken)
    console.log(config)
    console.log(localStorage.getItem('refreshToken'))
    axios.post(joinRoomUrl,{card, user},config)
        .then(res => { 
          console.log(res.data)
          return res.data;
        })
        .catch(error =>{alert(error)})

  }
  function joinRoom(card)
  {
    let join=isRoomFull(card)
    console.log(join)
    if(!join)
    {
      //window.location.href="/games/ticktactoe"
    }
    console.log(card)

  }  

  function handleSubmit(obj) {
    // check to see if the choosen room is full
    joinRoom(obj);
  };
  const renderCard= (card, index) =>{
    return(
        <ReactBoostrap.Card style={{ width: '18rem' }} key={index} className="box">
        <ReactBoostrap.Card.Img variant="top" style={{  height: '50%'}} src="holder.js/100px180" src={roomImg}/>
        <ReactBoostrap.Card.Body>
          <ReactBoostrap.Card.Title>{card.title}</ReactBoostrap.Card.Title>
          <ReactBoostrap.Card.Text>
              {card.text }<br></br> 
              {card.userCount + ' out of 2'}
          </ReactBoostrap.Card.Text>
          <ReactBoostrap.Button variant="primary" onClick={() => handleSubmit(card)} type="submit" >Join Room</ReactBoostrap.Button>
        </ReactBoostrap.Card.Body>
      </ReactBoostrap.Card>
     )
    }
  return (
    <div className="grid">
      {items && items.map(renderCard)}
    </div>
  );
}

export default GenerateRooms

