import React, { Component } from "react";
import Navbar from './components/Navbar'
import {BrowserRouter, Route} from 'react-router-dom'
import Home from './components/other_pages/home'
import GenerateRooms from './components/games/rooms'
import Ranking from './components/games/Features'
import TickTacToe from "./components/games/board_games/TickTacToe";
//import Contact from './components/Contact'
//import About from './components/About'
//import LoginApp from './components/LoginApp'



class Header extends Component {
  render() {

    return (
      <BrowserRouter>
        <div className="App">
          <Navbar />   
          <Route exact path='/' component={Home}/>
          <Route path = '/games/rooms' component={GenerateRooms}/>
          <Route path = '/ranking' component={Ranking}/>
          <Route path = '/games/ticktactoe'  component={TickTacToe}/>
        </div>
      </BrowserRouter>
    )
  }
}

export default Header;