import '../App.css';
import React, { useState } from 'react';
//import Modal from "react-responsive-modal";
import LoginApp from './login/loginApp'
import * as ReactBoostrap from "react-bootstrap";
import loginImg from "../login.png";
//import Example from './LoginModal'
function Navbar ()  {

  const [show, setShow] = useState(false);
  
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  
  return (
    <div className="App">
      <div className="container">
        <header className="main-navigation">
            <ReactBoostrap.Navbar  expand="xl" className="main-navbar">
              <ReactBoostrap.Navbar.Brand href="/">
              <img src={loginImg}  width="200" alt="" className="d-inline-block align-middle mr-2"/>
                <span className="text-uppercase font-weight-bold">Free Games Online</span>
              </ReactBoostrap.Navbar.Brand>  
                <ReactBoostrap.Navbar.Toggle aria-controls="basic-navbar-nav" />
                <ReactBoostrap.Navbar.Collapse id="basic-navbar-nav">
                  <ReactBoostrap.Nav className="mr-auto">
                  <ReactBoostrap.Nav.Link href="/">Home</ReactBoostrap.Nav.Link>
                  <ReactBoostrap.Nav.Link href="/games/rooms">Games</ReactBoostrap.Nav.Link>  
                  <ReactBoostrap.Nav.Link href="/ranking">Ranking</ReactBoostrap.Nav.Link>    
                    <ReactBoostrap.Form inline>
                    <ReactBoostrap.FormControl type="text" placeholder="Search" className="mr-sm-2" />
                    <ReactBoostrap.Button variant="outline-success">Search</ReactBoostrap.Button>
                  </ReactBoostrap.Form>
                  </ReactBoostrap.Nav>
                  <ReactBoostrap.Button variant="danger" className='loginorreg' onClick={handleShow}>Signup/Login</ReactBoostrap.Button>
                </ReactBoostrap.Navbar.Collapse>
            </ReactBoostrap.Navbar>
        </header>
        </div> 
        <ReactBoostrap.Modal
          className='modal'
          show={show}
          onHide={handleClose}
          keyboard={false}
        >
            <LoginApp/>
        </ReactBoostrap.Modal>

    </div>
    
    );
    
  }

  export default Navbar;
